import { css } from "@emotion/react";
import { graphql } from "gatsby";
import { Title } from "react-head";
import Block from "../../components/block";
import Layout from "../../components/layout";
import { PostData } from "../../components/post-list";
import { postStyle } from "../../lib/post-style";
import PageTransition from 'gatsby-plugin-page-transitions';
import Valine from 'gatsby-plugin-valine'
import { acr, colorScheme, dcr, lcr } from "../../lib/color";
import LikeBtn from "../../components/like-btn";
import CommentBtn from "../../components/comment-btn";
import { useEffect, useState } from "react";
import { getCommentCount, getLikeCount } from "../../lib/like";

export default function PostPage({ data, path }) {
  const postData: PostData = data.markdownRemark;
  const [likeCount, setLikeCount] = useState<number | null>(null);
  const [commentCount, setCommentCount] = useState<number | null>(null);

  useEffect(() => {
    getLikeCount(path).then(count => {
      setLikeCount(count);
    });
    getCommentCount(path).then(count => {
      setCommentCount(count);
    });
  }, [])

  return(
    <PageTransition
      transitionTime={500}
    >
      <Layout>
        <Title>{
          data.markdownRemark.frontmatter.title
            ? `${postData.frontmatter.title} - ${data.site.siteMetadata.title}`
            : data.site.siteMetadata.title
        }</Title>
        <Block>
          {postData.frontmatter.title?
            <h1 css={styles.title}>{postData.frontmatter.title}</h1>
          : null}
          <div
            css={postStyle}
            dangerouslySetInnerHTML={{__html: postData.html}}
          />
          <div css={styles.fbar}>
            <CommentBtn count={commentCount} />
            <LikeBtn path={path} count={likeCount} setCount={setLikeCount} style={{marginLeft: 10}} />
          </div>
        </Block>
        <Block>
          <Valine
            emojiMaps={null}
            lang="en"
            css={styles.comment}
            path={path}
          />
        </Block>
      </Layout>
    </PageTransition>
  )
}

export const pageQuery = graphql`
  query Post($id: String) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: {eq: $id}) {
      html
      frontmatter {
        title
      }
    }
  }
`

const styles = {
  title: css`
    font-size: 35px;
    font-weight: bold;
    margin: 0 0 15px 0;
  `,
  fbar: css`
    display: flex;
    position: sticky;
    bottom: 0;
    border-radius: 8px;
    padding: 15px 25px 15px 25px;
    margin: 10px -25px -15px -25px;
    background: #1C1C77;
    @supports ((backdrop-filter: blur(20px)) or (-webkit-backdrop-filter: blur(20px))) {
      -webkit-backdrop-filter: blur(20px);
      backdrop-filter: blur(20px);
      background: none;
    }
    @media screen and (max-width: 600px) {
      backdrop-filter: none;
      position: relative;
    }
  `,
  comment: css`
    .vemoji-btn {
      display: none !important;
    }
    .vwrap {
      border: none !important;
      padding: 0 !important;
      overflow: visible !important;
    }
    .vheader {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -5px;
    }
    .vheader .vinput {
      flex: 1;
      border: none !important;
      padding: 5px 10px !important;
      border-radius: 8px;
      font-size: 14px !important;
      width: auto !important;
      margin: 5px;
      background: ${lcr(0.05)} !important;
      transition: 200ms;
      color: ${colorScheme.fore} !important;
      ::placeholder {
        color: ${acr(0.5, colorScheme.fore)};
      }
      :hover {
        background: ${lcr(0.1)} !important;
      }
      :focus {
        background: ${lcr(0.1)} !important;
        box-shadow: 0 0 0 2px ${colorScheme.fore};
      }
    }
    .vrow {
      padding-bottom: 0 !important;
    }
    .vsubmit {
      border: none !important;
      border-radius: 15px !important;
      font-size: 15px !important;
      font-weight: bold !important;
      color: ${colorScheme.fore} !important;
      background: ${lcr(0.1)} !important;
      transition-duration: 100ms !important;
      -webkit-tap-highlight-color: transparent;
      @media (hover: hover) {
        :hover {
          background: ${lcr(0.2)} !important;
        }
      }
      :active {
        background: ${lcr(0.05)} !important;
      }
    }
    .vedit {
      padding-top: 5px !important;
    }
    .veditor {
      background: ${lcr(0.05)} !important;
      font-size: 16px !important;
      border-radius: 8px;
      padding: 5px 10px !important;
      color: ${colorScheme.fore} !important;
      ::placeholder {
        color: ${acr(0.4, colorScheme.fore)};
      }
      :hover {
        background: ${lcr(0.1)} !important;
      }
      :focus {
        background: ${lcr(0.1)} !important;
        box-shadow: 0 0 0 2px ${colorScheme.fore};
      }
    }
    .vempty {
      color: ${colorScheme.fore} !important;
    }
    .vreply-wrapper {
      padding: 0 3px;
    }
    .vicon {
      fill: ${colorScheme.fore} !important;
    }
    p {
      color: ${colorScheme.fore} !important;
    }
    .vcontent {
      font-size: 16px !important;
    }
    .vnick {
      color: inherit !important;
      cursor: auto !important;
    }
    a.vnick:hover {
      text-decoration: underline !important;
      cursor: pointer !important;
    }
    .vh {
      border: none !important;
    }
  `
}